.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@-webkit-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

.data {
  -webkit-animation: fade-in .3s forwards;
          animation: fade-in .3s forwards;
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-auto-rows: 150px;
  grid-gap: 50px;
  font-family: 微軟正黑體;
  -ms-flex-pack: start;
      justify-content: start;
  -ms-flex-align: start;
      align-items: start;
}

.data-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.data-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}
.reliability {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 3em;
  height: 3em;
  font-size: 2em;
}
.head {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 10px;
}
.title {
  font-size: 2em;
  color: blue;
  margin-right: 20px;
}
.source {
  font-size: 1.5em;
  color: gray;
  -ms-flex-item-align: end;
      align-self: flex-end;
}
.text {
  line-height: 1.3em;
  max-height: 2.4em;
  margin-bottom: 10px;
  font-size: 1.2em;
}
.interaction {
  font-size: 1em;
  color: gray;
}




